import React from 'react';
import { useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import ITEM_MENU from './list-item-menu';

const TODO_LIST = ({todolist}) => {
    const [list, updateList] = useState(todolist);
    useEffect(() => {
        updateList(todolist);
    }, [todolist])
    return <>
        <Box sx={{ display: 'flex', flex: '0 1 100%', flexWrap: 'wrap', height: 'fit-content', justifyContent: 'center'}}>
            {
                React.Children.toArray(
                    list.map((item, index) => {
                        return <>
                            <LIST_ITEM item={item} position={index} endoflist={index + 1 == +list.length}/>
                        </>
                    })
                )
            }
        </Box>
    </>
}

const LIST_ITEM = ({item, position, endoflist}) => {
    const [onEditState, setOnEditState] = useState(false);

    return <>
        <Box sx={{ display: 'flex', flex: '0 1 95%', padding: '5px 10px', marginBottom: '3px', alignItems: 'center', height: 'fit-content', color: 'white', borderRadius: '5px', backgroundColor: '#06198714'}}>
            <CheckCircleOutlineIcon sx={{color: item.status === 'done'? '#41ff00' : '#f5f4f45c'}} />
            {
                item.status === 'todo' && onEditState? <>
                    
                </> : <Typography sx={{marginLeft: '10px'}}>{item.title}</Typography>
            }
            <Box sx={{marginLeft: 'auto'}}>
                <ITEM_MENU item={item} position={position} endoflist={endoflist} />
            </Box>
        </Box>
    </>
}
export default TODO_LIST;